lass=\"app__navItem\"\n\t\t\t\t\t\t\t\t\t:class=\"\n\t\t\t\t\t\t\t\t\t\tsubmenuItem.isCurrent ? 'app__navItem--isCurrent' : ''\n\t\t\t\t\t\t\t\t\t\"\n\t\t\t\t\t\t\t\t\t:href=\"submenuItem.url\"\n\t\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t\t{{ submenuItem.name }}\n\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t</ul>\n\t\t\t\t\t</li>\n\t\t\t\t</ul>\n\t\t\t</nav>\n\t\t\t<main class=\"app__main\">\n\t\t\t\t<div class=\"app__page\">\n\t\t\t\t\t<nav\n\t\t\t\t\t\tclass=\"app__breadcrumbs\"\n\t\t\t\t\t\trole=\"navigation\"\n\t\t\t\t\t\taria-label=\"You are here:\"\n\t\t\t\t\t>\n\t\t\t\t\t\t<ol>\n\t\t\t\t\t\t\t<li\n\t\t\t\t\t\t\t\tv-for=\"(breadcrumb, index) in breadcrumbs\"\n\t\t\t\t\t\t\t\t:key=\"breadcrumb.name\"\n\t\t\t\t\t\t\t>\n\t\t\t\t\t\t\t\t<template v-if=\"index === breadcrumbs.length - 1\">\n\t\t\t\t\t\t\t\t\t<span aria-current=\"page\">{{ breadcrumb.name }}</span>\n\t\t\t\t\t\t\t\t</template>\n\t\t\t\t\t\t\t\t<template v-else>\n\t\t\t\t\t\t\t\t\t<a :href=\"breadcrumb.url\">\n\t\t\t\t\t\t\t\t\t\t{{ breadcrumb.name }}\n\t\t\t\t\t\t\t\t\t</a>\n\t\t\t\t\t\t\t\t\t<span class=\"app__breadcrumbsSeparator\">/</span>\n\t\t\t\t\t\t\t\t</template>\n\t\t\t\t\t\t\t</li>\n\t\t\t\t\t\t</ol>\n\t\t\t\t\t</nav>\n\t\t\t\t\t<div class=\"page__content\">\n\t\t\t\t\t\t<h1 class=\"app__pageHeading\">\n\t\t\t\t\t\t\tPage Title Here\n\t\t\t\t\t\t</h1>\n\t\t\t\t\t\t<div class=\"app__contentPanel\">\n\t\t\t\t\t\t\t<div style=\"min-height: 50rem;\"></div>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</main>\n\t\t</div>\n\t\t<div\n\t\t\taria-live=\"polite\"\n\t\t\taria-atomic=\"true\"\n\t\t\tclass=\"app__notifications\"\n\t\t\tref=\"notifications\"\n\t\t\trole=\"status\"\n\t\t>\n\t\t\t<transition-group name=\"app__notification\">\n\t\t\t\t<notification\n\t\t\t\t\tv-for=\"notification in notifications\"\n\t\t\t\t\t:key=\"notification.key\"\n\t\t\t\t\t:type=\"notification.type\"\n\t\t\t\t\t:can-dismiss=\"true\"\n\t\t\t\t\t@dismiss=\"dismissNotification(notification.key)\"\n\t\t