module.exports = "<template>\n\t<div class=\"previewModal\">\n\t\t<pkp-button ref=\"modalFormButton\" @click=\"$modal.show('form')\">\n\t\t\tModal with Form\n\t\t</pkp-button>\n\t\t<modal\n\t\t\tv-bind=\"MODAL_PROPS\"\n\t\t\tname=\"form\"\n\t\t\t@closed=\"setFocusToRef('modalFormButton')\"\n\t\t>\n\t\t\t<modal-content\n\t\t\t\tcloseLabel=\"Close\"\n\t\t\t\tmodalName=\"form\"\n\t\t\t\ttitle=\"Add Announcement\"\n\t\t\t>\n\t\t\t\t<pkp-form v-bind=\"form\" @set=\"setForm\" @success=\"formSuccess\" />\n\t\t\t</modal-content>\n\t\t</modal>\n\t</div>\n</template>\n\n<script>\nimport PkpForm from '@/components/Form/Form.vue';\nimport cloneDeep from 'clone-deep';\nimport form from '@/docs/components/Form/helpers/form-announcement';\nimport modal from '@/mixins/modal';\n\nexport default {\n\tmixins: [modal],\n\tcomponents: {\n\t\tPkpForm\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tform: {\n\t\t\t\t...cloneDeep(form),\n\t\t\t\taction: 'http://localhost:8080',\n\t\t\t\tmethod: 'GET'\n\t\t\t}\n\t\t};\n\t},\n\tmethods: {\n\t\tformSuccess(r) {\n\t\t\tthis.$modal.hide('form');\n\t\t},\n\t\tsetForm(key, data) {\n\t\t\tlet form = {...this.form};\n\t\t\tObject.keys(data).forEach(function(key) {\n\t\t\t\tform[key] = data[key];\n\t\t\t});\n\t\t\tthis.form = form;\n\t\t}\n\t}\n};\n</script>\n"