module.exports = "<template>\n\t<list-panel :items=\"items\" title=\"List Panel with Pagination\">\n\t\t<pagination\n\t\t\tv-if=\"lastPage > 1\"\n\t\t\tslot=\"footer\"\n\t\t\t:currentPage=\"currentPage\"\n\t\t\t:isLoading=\"isLoading\"\n\t\t\t:lastPage=\"lastPage\"\n\t\t\t@set-page=\"setPage\"\n\t\t/>\n\t</list-panel>\n</template>\n\n<script>\nimport ListPanel from '@/components/ListPanel/ListPanel.vue';\nimport Pagination from '@/components/Pagination/Pagination.vue';\n\nlet manyItems = [];\nfor (let i = 1; i < 31; i++) {\n\tmanyItems.push({\n\t\tid: i,\n\t\ttitle: 'Item ' + i\n\t});\n}\n\nexport default {\n\tcomponents: {\n\t\tListPanel,\n\t\tPagination\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tallItems: manyItems,\n\t\t\tcount: 5,\n\t\t\tisLoading: false,\n\t\t\titems: manyItems.slice(0, 5),\n\t\t\titemsMax: manyItems.length,\n\t\t\toffset: 0\n\t\t};\n\t},\n\tcomputed: {\n\t\tcurrentPage() {\n\t\t\treturn Math.floor(this.offset / this.count) + 1;\n\t\t},\n\t\tlastPage() {\n\t\t\treturn Math.ceil(this.itemsMax / this.count);\n\t\t}\n\t},\n\tmethods: {\n\t\tsetPage: function(page) {\n\t\t\t// Mock a delay for a remote request\n\t\t\tthis.isLoading = true;\n\t\t\tsetTimeout(() => {\n\t\t\t\tthis.offset = page * this.count - this.count;\n\t\t\t\tthis.items = this.allItems.slice(this.offset, this.count + this.offset);\n\t\t\t\tthis.isLoading = false;\n\t\t\t}, 1000);\n\t\t}\n\t}\n};\n</script>\n"