module.exports = "<template>\n\t<fieldset class=\"previewListPanelSelect\">\n\t\t<legend class=\"-screenReader\">List Panel with Select</legend>\n\t\t<list-panel :items=\"items\">\n\t\t\t<template slot=\"header\">\n\t\t\t\t<pkp-header>\n\t\t\t\t\t<h2>List Panel with Select</h2>\n\t\t\t\t</pkp-header>\n\t\t\t\t<div v-if=\"canSelectAll\" class=\"listPanel__selectAllWrapper\">\n\t\t\t\t\t<input\n\t\t\t\t\t\ttype=\"checkbox\"\n\t\t\t\t\t\t:id=\"id + '-selectAll'\"\n\t\t\t\t\t\t:checked=\"isSelectAllOn\"\n\t\t\t\t\t\t@click=\"toggleSelectAll\"\n\t\t\t\t\t/>\n\t\t\t\t\t<label class=\"listPanel__selectAllLabel\" :for=\"id + '-selectAll'\">\n\t\t\t\t\t\tSelect All\n\t\t\t\t\t</label>\n\t\t\t\t</div>\n\t\t\t</template>\n\t\t\t<template v-slot:item=\"{item}\">\n\t\t\t\t<div class=\"listPanel__itemSummary\">\n\t\t\t\t\t<label class=\"listPanel__selectWrapper\">\n\t\t\t\t\t\t<div class=\"listPanel__selector\">\n\t\t\t\t\t\t\t<input\n\t\t\t\t\t\t\t\ttype=\"checkbox\"\n\t\t\t\t\t\t\t\tname=\"submissions[]\"\n\t\t\t\t\t\t\t\t:value=\"item.id\"\n\t\t\t\t\t\t\t\tv-model=\"selected\"\n\t\t\t\t\t\t\t/>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t\t<div class=\"listPanel__itemIdentity\">\n\t\t\t\t\t\t\t<div class=\"listPanel__itemTitle\">\n\t\t\t\t\t\t\t\t{{ item.title }}\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t<div class=\"listPanel__itemSubTitle\">\n\t\t\t\t\t\t\t\t{{ item.subtitle }}\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</label>\n\t\t\t\t</div>\n\t\t\t</template>\n\t\t</list-panel>\n\t</fieldset>\n</template>\n\n<script>\nimport ListPanel from '@/components/ListPanel/ListPanel.vue';\nimport PkpHeader from '@/components/Header/Header.vue';\nimport items from '../helpers/items';\n\nexport default {\n\tcomponents: {\n\t\tListPanel,\n\t\tPkpHeader\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tid: 'previewListPanelSelect',\n\t\t\tcanSelectAll: true,\n\t\t\tisSelectAllOn: false,\n\t\t\titems: [...items],\n\t\t\tselected: []\n\t\t};\n\t},\n\tmethods: {\n\t\ttoggleSelectAll() {\n\t\t\tif (this.isSelectAllOn) {\n\t\t\t\tthis.selected = [];\n\t\t\t} else {\n\t\t\t\tthis.selected = this.items.map(i => i.id);\n\t\t\t}\n\t\t}\n\t},\n\twatch: {\n\t\tselected(newVal, oldVal) {\n\t\t\tthis.isSelectAllOn =\n\t\t\t\tthis.selected.length && this.selected.length === this.items.length;\n\t\t}\n\t}\n};\n</script>\n\n<style lang=\"less\">\n@import '../../../../styles/_import';\n\n.previewListPanelSelect {\n\tborder: none;\n\tpadding: 0;\n\n\t.listPanel__selectAllWrapper {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tmargin-top: 1rem;\n\t\tmargin-left: -0.5rem;\n\t\tline-height: 1.5rem;\n\n\t\t> input {\n\t\t\tmargin-left: 0.5rem;\n\t\t}\n\t}\n\n\t.listPanel__selectAllLabel {\n\t\tmargin-left: 0.5rem;\n\t}\n\n\t.listPanel__selectWrapper {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tmargin-left: -1rem;\n\t}\n\n\t.listPanel__selector {\n\t\twidth: 3rem;\n\t\tpadding-left: 1rem;\n\t}\n}\n</style>\n"