module.exports = "<template>\n\t<div class=\"previewModal\">\n\t\t<pkp-button ref=\"modalReportButton\" @click=\"$modal.show('report')\">\n\t\t\tModal with Custom Actions\n\t\t</pkp-button>\n\t\t<modal\n\t\t\tv-bind=\"MODAL_PROPS\"\n\t\t\tname=\"report\"\n\t\t\t@closed=\"setFocusToRef('modalReportButton')\"\n\t\t>\n\t\t\t<modal-content\n\t\t\t\tcloseLabel=\"Close\"\n\t\t\t\tmodalName=\"report\"\n\t\t\t\ttitle=\"Activity Report\"\n\t\t\t>\n\t\t\t\t<list>\n\t\t\t\t\t<list-item>\n\t\t\t\t\t\t<template slot=\"value\">\n\t\t\t\t\t\t\t<icon icon=\"comment-o\" :inline=\"true\" />\n\t\t\t\t\t\t\t23\n\t\t\t\t\t\t</template>\n\t\t\t\t\t\tEmails sent today\n\t\t\t\t\t</list-item>\n\t\t\t\t\t<list-item>\n\t\t\t\t\t\t<template slot=\"value\">\n\t\t\t\t\t\t\t<icon icon=\"file-text-o\" :inline=\"true\" />\n\t\t\t\t\t\t\t9\n\t\t\t\t\t\t</template>\n\t\t\t\t\t\tSubmissions rejected today\n\t\t\t\t\t</list-item>\n\t\t\t\t</list>\n\t\t\t\t<div class=\"modal__footer\">\n\t\t\t\t\t<spinner v-if=\"isSendingReport\" />\n\t\t\t\t\t<pkp-button\n\t\t\t\t\t\t:isDisabled=\"isSendingReport\"\n\t\t\t\t\t\t:isPrimary=\"true\"\n\t\t\t\t\t\t@click=\"sendEmailReport\"\n\t\t\t\t\t>\n\t\t\t\t\t\tSend Email Report\n\t\t\t\t\t</pkp-button>\n\t\t\t\t\t<pkp-button\n\t\t\t\t\t\t:isDisabled=\"isSendingReport\"\n\t\t\t\t\t\t@click=\"$modal.hide('report')\"\n\t\t\t\t\t>\n\t\t\t\t\t\tClose\n\t\t\t\t\t</pkp-button>\n\t\t\t\t</div>\n\t\t\t</modal-content>\n\t\t</modal>\n\t</div>\n</template>\n\n<script>\nimport List from '@/components/List/List.vue';\nimport ListItem from '@/components/List/ListItem.vue';\nimport modal from '@/mixins/modal';\n\nexport default {\n\tmixins: [modal],\n\tcomponents: {\n\t\tList,\n\t\tListItem\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tisSendingReport: false\n\t\t};\n\t},\n\tmethods: {\n\t\tsendEmailReport() {\n\t\t\tthis.isSendingReport = true;\n\t\t\t// Simulate a server request\n\t\t\tsetTimeout(() => {\n\t\t\t\tthis.isSendingReport = false;\n\t\t\t\tthis.$modal.hide('report');\n\t\t\t}, 2000);\n\t\t}\n\t}\n};\n</script>\n"